import React from 'react';
import { createConsoleLogger } from '../../utils';
import { UniformContextProvider } from '../../contexts';
import { CreateRenderingFromHtml } from '../CreateRenderingFromHtml';
// @ts-ignore
import { create } from 'react-test-renderer';

const testLogger = createConsoleLogger();

const GlobalTestRender = ({ children }: any) => {
    return (
        <UniformContextProvider componentMap={{}} logger={testLogger}>
            {children}
        </UniformContextProvider>
    );
};

export const renderContainerToJSON = (html: string) => {
    const result = create(
        <GlobalTestRender>
            <CreateRenderingFromHtml
                html={html}
                renderingContext={{} as any}
                rendering={{} as any}
                placeholderKey={'abcd'}
                index={0}
            />
        </GlobalTestRender>
    );
    return result.toJSON();
};
